import {
  getDataShape,
  ShowDataModal,
} from "../../../../actions/datamaster_action";
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ModalGlobal,
  Tabel,
  showModal,
  deleteData,
  hideModal,
  Swal,
  postDataNoTImeOut,
  ToastNotification,
  reset,
  putDataNoTImeOut
} from "../../../../components/helpers/library";
import HeadShape from "./HeadShape";

const maptostate = (state) => {
  return {
    data: state.datamaster.getDataShape,
  };
};
class BatuShape extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "kode_shape",
          text: "Kode Shape",
        },
        {
          dataField: "nama_shape",
          text: "Nama Shape",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_shape: row.kode_shape,
              nama_shape: row.nama_shape,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.editData(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() => this.hapusData(row)}
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    // console.log(data);

    this.setState({
      isLoading: true,
    });
    this.state.isEdit
      ? putDataNoTImeOut("batu-shape/"+data.id, data)
          .then((res) => {
            this.setState({
              isLoading: false,
            });
            this.props.dispatch(hideModal());
            this.props.dispatch(getDataShape());
            this.props.dispatch(reset());
            ToastNotification(
              "success",
              res.data.message || "Data Berhasil Disimpan"
            );
          })
          .catch((err) => {
            ToastNotification(
              "info",
              err.response?.data?.message || "Data Gagal Disimpan"
            );
            this.setState({
              isLoading: false,
            });
          })
      : postDataNoTImeOut("batu-shape", data)
          .then((res) => {
            this.setState({
              isLoading: false,
            });
            this.props.dispatch(hideModal());
            this.props.dispatch(getDataShape());
            this.props.dispatch(reset());
            ToastNotification(
              "success",
              res.data.message || "Data Berhasil Disimpan"
            );
          })
          .catch((err) => {
            ToastNotification(
              "info",
              err.response?.data?.message || "Data Gagal Disimpan"
            );
            this.setState({
              isLoading: false,
            });
          });
  }

  componentDidMount() {
    this.props.dispatch(getDataShape());
  }

  editData(data) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(data));
    this.setState({
      isEdit: true,
    });
  }
  showModal() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowDataModal(true));
    this.setState({
      isEdit: false,
    });
  }

  hapusData(row) {
    Swal.fire({
      // title: "Anda Yakin !!",
      // text: "Ingin Menghapus Data Ini ?",
      html:
        "Apakah Anda Yakin Ingin " +
        "Menghapus " +
        "<h1><b>Kode Shape " +
        row.kode_shape +
        "</b> ?</h1>",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
       deleteData('batu-shape/'+row._id).then((res)=>{
         ToastNotification("success",res.data.message || "Data Berhasil Dihapus")
         this.props.dispatch(getDataShape());
       }).catch((err)=>{
        ToastNotification("info",err.response?.data?.message || "Data Gagal Dihapus")
       })
      }
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item">
            <Link to="#">Data Berlian</Link>
          </li>
          <li className="breadcrumb-item active">Batu Shape</li>
        </ol>
        <h1 className="page-header">Batu Shape </h1>
        <Panel>
          <PanelHeader>Batu Shape</PanelHeader>
          <PanelBody>
            <Tabel
              keyField="kode_shape"
              tambahData={true}
              handleClick={() => this.showModal()}
              exportCsv={true}
              data={this.props.data}
              columns={this.state.columns}
              empty={this.props.data}
              textEmpty="Batu Shape Kosong"
            />
          </PanelBody>
        </Panel>

        <ModalGlobal
          size="P"
          title={this.state.isEdit ? "Edit Data Shape" : "Tambah Data Shape"}
          content={
            <HeadShape
              isLoading={this.state.isLoading}
              isEdit={this.state.isEdit}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
        />
      </div>
    );
  }
}

BatuShape = reduxForm({
  form: "BatuShape",
  enableReinitialize: true,
})(BatuShape);
export default connect(maptostate)(BatuShape);
