import {
  Field,
  React,
  connect,
  reduxForm,
  Loading,
  ReanderField,
  hideModal,
  HiidenFiled
} from "../../../../components/helpers/library";
import ValidasiMasterBerlian from "../../../../Validasi/ValidasiMasterBerlian";

const maptostate = (state) => {
  return {
    initialValues: {
      id: state.datamaster.ShowDataModal.id,
      kode_shape: state.datamaster.ShowDataModal.kode_shape,
      nama_shape: state.datamaster.ShowDataModal.nama_shape,
    },
  };
};
class HeadShape extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit() {}

  componentDidMount() {
    setTimeout(() => {
      if (this.props.isEdit) {
        document.getElementById("nama_shape").focus();
      } else {
        document.getElementById("kode_shape").focus();
      }
    }, 100);
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          id="id"
          name="id"
          component={HiidenFiled}
          type="hidden"
        />
        <Field
          id="kode_shape"
          tabIndex="1"
          name="kode_shape"
          component={ReanderField}
          type="text"
          label="Kode Shape"
          readOnly={this.props.isEdit}
          placeholder="Masukan Kode Shape"
        />
        <Field
          id="nama_shape"
          tabIndex="2"
          name="nama_shape"
          component={ReanderField}
          type="text"
          label="Nama Shape"
          placeholder="Masukan Nama Shape"
          onBlur={() => this.setState({ aktif: true })}
        />

        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadShape = reduxForm({
  form: "HeadShape",
  enableReinitialize: true,
  validate: ValidasiMasterBerlian,
})(HeadShape);
export default connect(maptostate)(HeadShape);
